
.app-list{list-style:none;padding:0;margin:0
}
.app-list .app-item{position:relative
}
.app-list .app-item img{width:4rem;height:4rem
}
.app-list .app-item .app-list-content{margin-left:5rem;padding:1rem 4rem 1rem 0;overflow:hidden
}
.app-list .app-item .app-list-content div{line-height:1
}
.app-list .app-item .app-list-content div h1{display:inline-block;font-size:1.4rem;vertical-align:middle
}
.app-list .app-item .app-list-content div h1 .top-three{color:#F7BA2A
}
.app-list .app-item .app-list-content div>span{display:inline-block;margin-left:0.4rem;padding:0.2rem 0.3rem;color:#FFF;background-color:#20A0FF;border-radius:0.2rem;vertical-align:middle
}
.app-list .app-item .app-list-content p{margin-top:0.6rem
}
.app-list .app-item .app-list-content:after{left:5rem
}
.app-list .app-item .el-button.el-button--small{font-size:1rem;padding:0.4rem 0.8rem;border-radius:0.4rem;border-width:0.2rem;height:2.2rem
}



